// Subscription plugin
import { getSubscribedServices } from '../../../../subscription/interface/selectors'

export default function createAnonOperations(container) {
  const { context, SubscriptionOperations } = container

  /**
   * When an anonymous call ends, the user should be unsubscribed since they
   *    were only authenticated for the call.
   * @method anonymousCallEnd
   */
  async function anonymousCallEnd() {
    // Retrieve needed info from the store.
    const activeServices = getSubscribedServices(context.getState())

    await SubscriptionOperations.unsubscribeFlow(activeServices)
  }

  return anonymousCallEnd
}
